import React from "react";
import classes from "./Presentation.module.css";
import classes2 from "../../login/Login.module.css";
import {CreateField, Input, Textarea} from "../../common/FormsControls/FormsControls";
import {reduxForm} from "redux-form";

export const ProfileInfoForm = ({handleSubmit, profile, error}) => {
    return (
        <form onSubmit={handleSubmit}>
            <div><button>Save</button></div>
            {error && <div className={classes2.error}>{error}</div>}
            <div className={classes.about}><b>Full name:</b> {CreateField('Full name', 'fullName', [], Input)}</div>
            <div className={classes.about}><b>Looking for a job:</b> {CreateField('', 'lookingForAJob', [], Input, {type: 'checkbox'})}</div>
            <div><b>Professional Skills:</b> {CreateField('Skills', 'lookingForAJobDescription', [], Textarea)}</div>
            <div className={classes.about}><b>About Me:</b> {CreateField('About Me', 'aboutMe', [], Textarea)}</div>
            <div className={classes.about}><h3>Contacts:</h3> {Object.keys(profile.contacts).map(key => {
                return <div key={key} className={classes.contact}><h5>{key}: {CreateField('Contact', `contacts.${key}`, [], Input)}</h5></div>
            })}</div>
        </form>
    )
}


export const ProfileInfoReduxForm = reduxForm({form: 'profileEdit'})(ProfileInfoForm)