import classes from "../CreatePost/CreatePost.module.css"
import React from "react";
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, requiredField} from "../../../utils/validators/validators";
import {Textarea} from "../../common/FormsControls/FormsControls";


const maxLength = maxLengthCreator(80)

const CreatePost = props => {
    const post = post => {
        props.addPost(post.text)
    }

    return (
        <div className={classes.create}>
            <CreatePostReduxForm onSubmit={post} />
        </div>
    )
}

const CreatePostForm = props => {
    return (
        <form name={'post'} onSubmit={props.handleSubmit}>
            <div>
                <label>New post</label>
                <Field component={Textarea} validate={[requiredField, maxLength]} placeholder='Type here' name={'text'} className={classes.content} />
            </div>
            <div>
                <button className={classes.button}>Create</button>
            </div>
        </form>
    )
}

const CreatePostReduxForm = reduxForm({form: 'post'})(CreatePostForm)

export default CreatePost