import React from "react";
import classes from "../../profile/CreatePost/CreatePost.module.css";
import {Field} from "redux-form";


const FormControl = ({input, meta: {touched, error}, children}) => {
    return (
        <div>
            {children}
            {touched && error ? <p className={classes.errorSpan}>{error}</p> : null}
        </div>
    )
}

export const Textarea = ({input, child, ...props}) => {
    return <FormControl {...props}><textarea {...input} {...props}/></FormControl>
}

export const Input = ({input, child, ...props}) => {
    return <FormControl {...props}><input {...input} {...props}/></FormControl>
}

export const CreateField = (placeholder, name, validators, component, type = {}) => {
    return (
        <div>
            <Field placeholder={placeholder}
                   name={name}
                   validate={[...validators]}
                   component={component}
                   {...type}
            />
        </div>
    )
}