import classes from "../Presentation/Presentation.module.css";
import Preloader from "../../common/preloader/Preloader";
import ProfileStatusWithHooks from "./Info/ProfileStatusWithHooks";
import {ImageHover} from "../../common/imageHover/imageHover";
import React, {useState} from "react";
import {ProfileInfoReduxForm} from "./ProfileInfoForm";

const Presentation = ({profile, status, updateStatus, isOwner, saveImage, saveProfile}) => {
    let [editMode, setEditMode] = useState(false)

    if (!profile) {
        return <Preloader/>
    }


    const activateEditMode = () => {
        setEditMode(true)
    }

    const onSubmit = formData => {
        // saveProfile(formData)
        //     .then(() => {setEditMode(false)})
        saveProfile(formData)
        setEditMode(false)
    }

    const onProfileImageSelected = e => {
        if (e.target.files.length) {
            saveImage(e.target.files[0])
        }
    }

    return (
        <div className={classes.presentation}>
            <div className={classes.imageField}>
                <img className={classes.avatar}
                     src={profile.photos.large || 'https://www.enduresc.com.au/wordpress/wp-content/uploads/2018/05/user-profile.jpg'}
                     alt="llll"/>
                {isOwner && <ImageHover/>}
                {isOwner &&
                <div><input className={classes.input} type={'file'} onChange={onProfileImageSelected}/></div>}
            </div>
            <div className={classes.info}>
                <ProfileStatusWithHooks profile={profile} status={status} updateStatus={updateStatus}/>
                {editMode ? <ProfileInfoReduxForm initialValues={profile} profile={profile} onSubmit={onSubmit}/> :
                    <ProfileInfo profile={profile} isOwner={isOwner} activateEditMode={activateEditMode}/>}
            </div>
        </div>
    )
}

const ProfileInfo = ({profile, isOwner, activateEditMode}) => {
    return (
        <div>
            {isOwner && <div>
                <button onClick={activateEditMode}>Edit</button>
            </div>}
            <div className={classes.about}><b>Looking for a job:</b> {profile.lookingForAJob ? 'YES' : 'NO'}</div>
            {
                profile.lookingForAJob && <div>
                    <b>Professional Skills:</b> {profile.lookingForAJobDescription ? 'YES' : 'NO'}
                </div>
            }
            <div className={classes.about}><b>About Me:</b> {profile.aboutMe}</div>
            <div className={classes.about}><h3>Contacts:</h3> {Object.keys(profile.contacts).map(key => {
                return <Contact title={key} value={profile.contacts[key]}/>
            })}</div>
        </div>
    )
}

const Contact = ({title, value}) => {
    return (
        <div className={classes.contact}><h5>{title}: <a href={`https://${value}`}>{value}</a></h5></div>
    )
}


export default Presentation