import classes from "./ProfileStatus.module.css";
import React, {useEffect, useState} from "react";

const ProfileStatusWithHooks = ({profile, ...props}) => {
    let [editMode, setEditMode] = useState(false)
    let [status, setStatus] = useState(props.status)

    useEffect(() => setStatus(props.status), [props.status])

    const activateEditMode = () => {
        setEditMode(true)
    }

    const deactivateEditMode = () => {
        setEditMode(false)
        props.updateStatus(status)
    }

    const onChange = (e) => {
        setStatus(e.currentTarget.value)
    }

    return (
        <div>
            <div className={classes.info}>
                <div className={classes.name}><h2>{profile.fullName}</h2></div>
                {!editMode && <div className={classes.about}><p
                    onDoubleClick={activateEditMode}>{props.status || '................'}</p></div>}
                {editMode &&
                <div><input className={classes.input} autoFocus={true} onChange={onChange} onBlur={deactivateEditMode}
                            value={status}/></div>}
            </div>
        </div>
    )
}

