import Profile from "./Profile";
import React from "react";
import {connect} from "react-redux";
import {getProfile, getStatus, saveImage, saveProfile, updateStatus} from "../../redux/profileReducer";
import {withRouter} from "react-router-dom"
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";


class ProfileContainer extends React.Component {
    refreshProfile() {
        let userId = this.props.match.params.userId
        if (!userId) {
            userId = this.props.authorizedUserId
            if (!userId) this.props.history.push('/login')
        }
        this.props.getProfile(userId)
        this.props.getStatus(userId)
    }

    componentDidMount() {
        this.refreshProfile()
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.match.params.userId !== prevProps.match.params.userId) {
            this.refreshProfile()
        }
    }

    render() {
        return <Profile {...this.props} isOwner={!this.props.match.params.userId} profile={this.props.profile} staus={this.props.status} updateStatus={this.props.updateStatus} saveImage={this.props.saveImage}/>
    }
}


const mapStateToProps = state => {
    return {
        profile: state.profilePage.profile,
        status: state.profilePage.status,
        authorizedUserId: state.auth.userId,
        isAuth: state.auth.isAuth
    }
}


 export default compose(withRouter, withAuthRedirect, connect(mapStateToProps, {getProfile, getStatus, updateStatus, saveImage, saveProfile}))(ProfileContainer)